<template>
	<view class="warp">
		<view class="type-close ">
			<picker @change="bindPickerChange" range-key="name" :value="index" :range="typeList">
				<view class="uni-input">{{typeList[index].name}}</view>
			</picker>
		</view>
		<view class="inpt-content">
			<textarea maxlength="800" placeholder="1.点击上面框框选择分类 2.输入你想要的信息 3.点击发表" v-model="content" class="inpt-text"></textarea>
		</view>
	
		<view class="inpt-imge">
			<uni-file-picker 
				:auto-upload="false"
				class="upload"
				v-model="imageValue"
				fileMediatype="image" 
				mode="grid" 
				:limit="6"
				@select="select" 
				@progress="progress" 
				@success="success" 
				@fail="fail" 
				@delete="remove"
				
			>
			<image style="width: 30px;height: 30px;" src="../../static/images/icon/pic.png"></image>
			</uni-file-picker>
		</view>
		
		<view @click="sead()" class="inpt-sead">
			<span v-if="loading==0">发表</span>
			<span v-else-if="loading==1">发表中</span>
			<span v-else>图片上传中</span>
		</view>
	

	</view> 
</template>

<script>
	import {getTypeList} from "@/api/talk.js"
	export default {
		data() {
			return {
				typeList:[],
				topicId:null,
				index:0,
				typeId:"",
				content:"",
				loading:0,
				imageValue:[]
			}
		},
		onLoad(op) {
			this.topicId = op.topicId
			getTypeList().then(res=>{
				this.typeList=res.data;
			})
		},
		methods: {
			bindPickerChange(e) {
			   this.index =  e.detail.value
			},
			// 获取上传状态
			async select(e){
				//和提交一起上传
				e.tempFiles.forEach(file=>{
					const param = {
						"name":'talk/' + file.name,
						"extname":file.extname,
						"url":file.url
					}
					this.imageValue.push(param);
				});
			},
			// 获取上传进度
			progress(e){
				// console.log('上传进度：',e)
				
			},
			// 上传成功
			success(e){
				// console.log('上传成功')
			},
			// 上传失败
			fail(e){
				// console.log('上传失败：',e)
			},
			//移除文件
			remove(e){
				console.log(e)
				 // console.log(this.imageValue)
				 const num = this.imageValue.findIndex(v => v.url === e.tempFilePath);
				 this.imageValue.splice(num, 1);
				
			},
			async sead(){
				if(this.loading!=0)
					return;
			
				if(this.stringUtils.isEmpty(this.content)){
					uni.showToast({
						title: "内容不能为空！",
						icon:"none",
						duration:850
					});
					return;
				}
				
				this.loading=1;
				//异步执行
				this.$store.dispatch("addTalk",{
					images:[...this.imageValue],
					params:{
						typeId:this.typeList[this.index].id,
						content:this.content,
						pictrueList:[]
					}
				})
				wx.showModal({
				  title: '提示',
				  content: '发送成功，已提交审核，审核时间1-2分钟',
				  success (res) {
				   uni.navigateBack();
				  }
				})
			},
		}
	}
</script>

<style>
.inpt-text{
	width: calc( 100% - 20px );
	margin-top: 10px;
	padding: 10px; 
	height: 300rpx;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
	border-radius: 10px;
}
.inpt-sead{
	width: 100%;
	height: 100rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #fccd43;
	border-radius: 10px;
	
}
.inpt-imge{
	margin: 100rpx 0;
	
}
.uni-file-picker{

}
.type-close{
	margin: 20px;
	
	display: flex;
	justify-content: center;
	padding: 10px;
	font-size: 16px;
	border-style: solid;
	border-color: black;
	border-width: 1px;
	border-radius: 10px;
}
</style>
